import React, { useState } from "react";
import Markdown from "markdown-to-jsx";
import { motion, AnimatePresence } from "framer-motion";
import {
  ButtonsWrapper,
  Divider,
  MenuWrapper,
  TextButton,
  TextWrapper,
} from "./PickMenu.styled";
import { IContent, IEmployer, IProject, NonEmptyArr } from "../../types";

interface PickMenuProps {
  content: NonEmptyArr<IContent | IEmployer | IProject>;
  markdown: boolean;
}

const PickMenu = ({ content, markdown }: PickMenuProps) => {
  const [currentContentIndex, setCurrentContentIndex] = useState(0);
  return (
    <MenuWrapper>
      <ButtonsWrapper>
        {content.map(({ name, id }, index) => (
          <TextButton
            $active={currentContentIndex === index}
            type="button"
            key={id}
            onClick={() => setCurrentContentIndex(index)}
          >
            <motion.span initial={{ opacity: 0 }} whileInView={{ opacity: 1 }}>
              {name}
            </motion.span>
          </TextButton>
        ))}
      </ButtonsWrapper>
      <Divider />
      {markdown ? (
        <TextWrapper initial={{ opacity: 0 }} whileInView={{ opacity: 1 }}>
          <AnimatePresence mode="wait">
            <motion.article
              key={currentContentIndex}
              initial={{ opacity: 0, position: "relative", left: 50 }}
              animate={{ opacity: 1, left: 0 }}
              whileInView={{ opacity: 1 }}
              exit={{ bottom: -150, opacity: 0 }}
              transition={{
                bottom: { duration: 0.2 },
                opacity: { duration: 0.1 },
              }}
            >
              <Markdown>{content[currentContentIndex].value}</Markdown>
            </motion.article>
          </AnimatePresence>
        </TextWrapper>
      ) : (
        <TextWrapper>
          <motion.article
            key={currentContentIndex}
            initial={{ opacity: 0, position: "relative", left: 200 }}
            animate={{ opacity: 1, left: 0 }}
            whileInView={{ opacity: 1 }}
          >
            {content[currentContentIndex].value}
          </motion.article>
        </TextWrapper>
      )}
    </MenuWrapper>
  );
};

export default PickMenu;
